import React from "react";
import ReactDOM from "react-dom";
import {
  Button,
  Div,
  Root,
  CellButton,
  View,
  Panel,
  PanelHeader,
  PanelHeaderBack,
  Header,
  Group,
  Cell,
} from "@vkontakte/vkui";
import "@vkontakte/vkui/dist/vkui.css";
import { questions } from "./data/data";
import ReactImageMagnify from "react-image-magnify";
import bridge from "@vkontakte/vk-bridge";

bridge.send("VKWebAppInit", {});

class Quiz extends React.Component {
  constructor(props) {
    super(props);
    this.questions = questions;

    this.state = {
      activeView: "introView",
      currentStep: 0,
      answers: [],
    };
  }

  returnToBeginning() {
    this.setState({
      activeView: "introView",
      currentStep: 0,
      answers: []
    });
  }

  calculateCorrectAnswers() {
    let correct = 0;
    for (let i = 0; i < this.state.answers.length; i++) {
      if (this.state.answers[i] === questions[i].answer) {
        correct++;
      }
    }
    return correct;
  }

  currentAnswerIsCorrect() {
    const currentQuestion = questions[this.state.currentStep];
    return (
      this.currentQuestionAnswered() &&
      this.state.answers[this.state.answers.length - 1] ===
      currentQuestion.answer
    );
  }

  finished() {
    return this.questions.length <= this.state.currentStep + 1;
  }

  currentQuestionAnswered() {
    return this.state.answers.length > this.state.currentStep;
  }

  answerQuestion(answer) {
    let answers = this.state.answers.slice();
    answers[answers.length] = answer;
    this.setState({
      answers: answers,
      activeView: "answerView",
    });
  }

  nextQuestion() {
    debugger;
    if (this.finished()) {
      this.setState({
        activeView: 'summaryView'
      });
    } else {
      this.setState({
        activeView: 'questionView',
        currentStep: this.state.currentStep + 1,
      });
    }
  }

  returnToQuestion() {
    this.setState({
      activeView: "questionView",
    });
  }

  renderQuestionButtons() {
    if (this.currentQuestionAnswered()) {
      return (
        <CellButton
          align="center"
          // style={{
          //   display: "block",
          //   marginLeft: "auto",
          //   marginRight: "auto",
          // }}
          onClick={() => this.nextQuestion()}
        >
          Следующий вопрос
        </CellButton>
      );
    } else {
      return (
        <Div
          style={{
            display: "flex",
            maxWidth: "550px",
            marginLeft: "auto",
            marginRight: "auto",
          }}
        >
          <Button
            size="m"
            stretched
            style={{ marginRight: 8 }}
            onClick={() => this.answerQuestion(true)}
          >
            Да
          </Button>
          <Button size="m" stretched onClick={() => this.answerQuestion(false)}>
            Нет
          </Button>
        </Div>
      );
    }
  }

  render() {
    const currentQuestion = questions[this.state.currentStep];
    return (
      <Root activeView={this.state.activeView}>
        <View activePanel="introPanel" id="introView">
          <Panel id="introPanel">
            <PanelHeader>Гобелены</PanelHeader>
            <Div style={{ paddingTop: 60, paddingBottom: 60, maxWidth: 600, display: 'block', marginLeft: 'auto', marginRight: 'auto' }}>
              Lorem ipsum dolor sit amet, consectetur adipiscing elit. Sed a
              sollicitudin lectus, a commodo sapien. Vivamus a urna leo. Integer
              iaculis dignissim urna, sit amet vestibulum diam bibendum a. Donec
              eu arcu ut augue porttitor faucibus. Vestibulum nec pretium
              tortor, sit amet congue nunc. Aenean ullamcorper ex sem, sed
              interdum quam consequat et. Vestibulum a ex non diam fringilla
              feugiat. Nunc eu tellus sed leo elementum cursus. Mauris blandit
              porta egestas.
            </Div>
            <Group>
              {/* <CellButton onClick={ () => this.setState({ activeView: 'view2' }) }> */}
              <CellButton align="center"
                onClick={() => this.setState({ activeView: "questionView" })}
              >
                Пройти тест
              </CellButton>
            </Group>
          </Panel>
        </View>
        <View activePanel="questionPanel" id="questionView">
          <Panel id="questionPanel">
            <PanelHeader>Это гобелен?</PanelHeader>
            <Group>
              <Div style={{ display: "flex" }}>
                <ReactImageMagnify
                  style={{
                    width: "90%",
                    maxWidth: "600px",
                    display: "block",
                    marginLeft: "auto",
                    marginRight: "auto",
                  }}
                  {...{
                    smallImage: {
                      src: currentQuestion.image,
                      isFluidWidth: true,
                      sizes:
                        "(max-width: 480px) 100vw, (max-width: 1200px) 30vw, 360px",
                    },
                    largeImage: {
                      src: currentQuestion.largeImage,
                      width: 2000,
                      height: 1600,
                    },
                    lensStyle: { backgroundColor: "rgba(0,0,0,.6)" },
                    hintTextTouch:
                      "Нажмите и удерживайте на изображении для увеличения",
                    hintTextMouse:
                      "Наведите курсор на изображение для увеличения",
                    isHintEnabled: true,
                    enlargedImagePosition: "over",
                  }}
                ></ReactImageMagnify>
              </Div>
              {this.renderQuestionButtons()}
            </Group>
          </Panel>
        </View>
        <View activePanel="answerPanel" id="answerView">
          <Panel id="answerPanel">
            <PanelHeader
              left={<PanelHeaderBack onClick={() => this.returnToQuestion()} />}
            >
              {this.currentAnswerIsCorrect()
                ? currentQuestion.descriptionTrue
                : currentQuestion.descriptionFalse}
              !
            </PanelHeader>
            <Div style={{ paddingTop: 60, paddingBottom: 60, maxWidth: 600, display: 'block', marginLeft: 'auto', marginRight: 'auto' }}>
              {currentQuestion.description}
            </Div>
            <Div style={{display: 'flex', marginLeft: 'auto', marginRight: 'auto'}}>
              <img style={{display: 'flex', marginLeft: 'auto', marginRight: 'auto'}} src={currentQuestion.detailImage}/>
            </Div>
            <CellButton align='center' onClick={() => this.returnToQuestion()}>Назад</CellButton>
            <CellButton align='center' onClick={() => this.nextQuestion()}>Следующий вопрос</CellButton>
          </Panel>
        </View>
        <View activePanel="summaryPanel" id="summaryView">
          <Panel id="summaryPanel">
            <PanelHeader>
              Правильных ответов: {this.calculateCorrectAnswers()} / {this.state.answers.length}
            </PanelHeader>
            <Div style={{ paddingTop: 60, paddingBottom: 60, maxWidth: 600, display: 'block', marginLeft: 'auto', marginRight: 'auto' }}>
              Lorem ipsum dolor sit amet, consectetur adipiscing elit. Sed a
              sollicitudin lectus, a commodo sapien. Vivamus a urna leo. Integer
              iaculis dignissim urna, sit amet vestibulum diam bibendum a. Donec
              eu arcu ut augue porttitor faucibus. Vestibulum nec pretium
              tortor, sit amet congue nunc. Aenean ullamcorper ex sem, sed
              interdum quam consequat et. Vestibulum a ex non diam fringilla
              feugiat. Nunc eu tellus sed leo elementum cursus. Mauris blandit
              porta egestas.
            </Div>
            <CellButton align='center' onClick={() => this.returnToBeginning()}>В начало</CellButton>
          </Panel>
        </View>
      </Root>
    );
  }
}

ReactDOM.render(<Quiz />, document.getElementById("root"));
